.container{height:205vh;position:relative}.heroInner{height:100vh;position:sticky;top:0;display:flex;flex-direction:row;align-items:center;background:#000;color:#fff}@media(max-width: 640px){.heroInner{flex-direction:column}}.leftText{flex:1;display:flex;flex-direction:column;justify-content:flex-start;padding:clamp(1.5rem,5vw,4rem);z-index:10;min-height:100vh}@media(max-width: 640px){.leftText{min-height:auto}}.rightVideo{flex:1;display:flex;align-items:center;justify-content:center;overflow:hidden}@media(max-width: 640px){.rightVideo{order:-1;min-height:100vh}}.video{height:100vh;width:auto;object-fit:cover}.headlinePrimary{position:relative;font-family:var(--font-inter);font-size:3rem;font-weight:600;line-height:1.2;margin-bottom:4rem;display:flex;flex-direction:column;align-items:flex-start;justify-content:flex-start;white-space:pre-wrap;gap:.8rem;height:11rem}.lineTop{min-height:3.6rem;font-size:2rem;line-height:1.2;width:100%;font-weight:300}.lineBottom{min-height:6.6rem;width:100%}.wordHighlight{font-family:var(--font-cormorant);font-size:6rem;line-height:1.1;display:inline-block}@media(max-width: 640px){.wordHighlight{font-size:4rem}}.headlineSecondary{font-family:var(--font-inter);font-size:1.25rem;font-weight:500;line-height:1.5;margin-bottom:2rem}.introParagraph,.outroParagraph{font-family:var(--font-inter);font-size:1rem;line-height:1.6;max-width:600px}.ctaButtons{display:flex;flex-wrap:wrap;gap:1rem;margin-top:2rem}.cta{font-family:var(--font-inter);padding:1rem 2rem;font-weight:600;text-transform:uppercase;border-radius:8px;text-decoration:none;transition:all .2s ease;display:inline-block;text-align:center}.cta.solid{background:#fff;color:#000;border:none}.cta.solid:hover{background:#eee;transform:translateY(-2px)}.cta.outlined{background:rgba(0,0,0,0);color:#fff;border:2px solid #fff}.cta.outlined:hover{background:hsla(0,0%,100%,.1);transform:translateY(-2px)}.cursor{display:inline-block;width:1px;height:1em;background-color:currentColor;margin-left:2px;animation:blink 1s steps(1) infinite}@keyframes blink{0%,50%{opacity:1}50.01%,100%{opacity:0}}.introBlock,.outroBlock{width:100%;margin-top:auto;transition:opacity .4s ease,transform .4s ease}